<template>
  <div>
    <div style="text-align:center;"><h3></h3></div>
    <el-form v-if="isHasData" ref="form" :model="form" label-width="150px">
      <el-form-item label="" required>
        <el-select v-model="form.choose" clearable placeholder="">
          <el-option
              v-for="item in form.roles"
              :key="item.value"
              :label="item.label"
              :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="()" required>
        <el-input  v-model="form.username"></el-input>
      </el-form-item>
      <el-form-item  label="" >
        <el-tooltip class="item" effect="dark" content="" placement="right">
          <el-input v-model="form.password" show-password></el-input>
        </el-tooltip>
      </el-form-item>
      <el-form-item label="" required>
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <template>
          <el-radio v-model="form.sex" label="0"></el-radio>
          <el-radio v-model="form.sex" label="1"></el-radio>
          <el-radio v-model="form.sex" label="2"></el-radio>
        </template>
      </el-form-item>

      <el-form-item label="" required>
        <el-input v-model="form.phoneNumber"></el-input>
      </el-form-item>
      <el-form-item label="">

        <el-input placeholder="" v-model="form.identity" ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-switch
            v-model="form.locked"
            active-color="#ff4949"
            inactive-color="#13ce66"
            active-text=""
            inactive-text="">
        </el-switch>
      </el-form-item>
      <el-form-item label="">
        <el-switch
            v-model="form.enable"
            active-color="#13ce66"
            inactive-color="#ff4949"
            active-text=""
            inactive-text="">
        </el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button></el-button>
      </el-form-item>
    </el-form>
    <div v-else style="text-align: center;color: red;font-size: 30px;padding: 50px 0">
      <h1 ></h1>
    </div>

  </div>
</template>

<script>
export default {
  name: "InsertRenter",
  data() {
    return {
      isHasData:false,
      form: {
        choose:'',
        roles:[
          {
            value:1,
            label:""
          },       {
            value:2,
            label:""
          },       {
            value:3,
            label:""
          },
        ],
        username:'',
        password:'',
        sex:'2',
        name:'',
        phoneNumber:'',
        identity:'',
        locked:false,
        enable:true,
      }
    }
  },
  computed:{
    tableData(){
      return JSON.parse(this.$route.query.tableData)
    }
  },
  methods:{
    onSubmit(){
      this.$message.success("")
    }
  },
  created() {
    if (this.$route.query.isHasData!==undefined) {
      this.isHasData = true;
      let data = this.tableData;
      this.form.choose = data.roleId
      this.form.username = data.username
      this.form.password = data.password
      this.form.sex = data.sex
      this.form.name = data.name
      this.form.phoneNumber = data.phoneNumber
      this.form.identity = data.identity
      this.form.locked = data.locked
      this.form.enable = data.enable
      this.userId = this.$route.query.userId
    }
  }
}
</script>

<style scoped>
.el-form {
  margin-top: 20px;
  padding: 0 50px;
}
.el-input{
  width: 24%;
}
</style>