<template>
  <div>

    <div style="text-align:center;"><h3></h3></div>
    <header style="margin: 10px 0">
      <el-row>
        <el-col :span="6">
          <div class="grid-content bg-purple">&nbsp;</div>
        </el-col>
        <el-col :span="6">

        </el-col>
        <el-col :span="6">
          <el-input v-model="search" placeholder=""></el-input>
        </el-col>
        <el-col :span="6" style="text-align: center">
          <el-button type="success" round></el-button>
        </el-col>
      </el-row>
    </header>
    <el-table
        :data="filterData"
        style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item style="color: black;margin-left: 10%">
              <h3></h3>
            </el-form-item>
            <el-form-item label=" ID">
              <span>{{ props.row.userId }}</span>
            </el-form-item>
            <el-form-item label=" ID">
              <span>{{ props.row.roleId }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.roleDescription }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.username }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.sex==="2"?"":props.row.sex==="1"?"":"" }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.phoneNumber }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.identity }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.locked ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.enable ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.rentState ? '' : '' }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column label=""
                       prop="username">
      </el-table-column>
      <el-table-column label=""
                       prop="name"
      >
      </el-table-column>
      <el-table-column label=""

                       prop="phoneNumber">
      </el-table-column>
      <el-table-column label=""
                       prop="houseState.rentState">
        <template slot-scope="props">
        <span>
          {{ props.row.rentState ? '' : '' }}
        </span>
        </template>
      </el-table-column>
      <el-table-column
          prop="address"
          label="">
        <template slot-scope="props">
          <div>
            <el-button
                type="primary" round
                style="width: 45%;"
                @click="$router.push({
                path:'/manage/updateRenter',
                query:{
                  isHasData:true,
                  tableData:JSON.stringify(filterData[props.$index])
                }
                })"
            >
            </el-button>
            <el-button
                type="danger" round
                style="width: 45%;"
                @click="deleteRenter(props.row.userId)"
            >
            </el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "QueryRenter",
  data() {
    return {
      search: '',
      tableData: []
    }
  },
  computed: {
    filterData() {
      return this.tableData.filter((item) => {
        return item.username.indexOf(this.search) !== -1
      })
    }
  },
  methods: {
    deleteRenter(userId) {
      let i;
      for (i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].userId === userId) {
          break;
        }
      }
      this.$confirm(`${this.tableData[i].username}${this.tableData[i].name}, `,
          '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'error'
          }).then(() => {
            this.$axios.get(this.$store.state.publicLocation+"/manage/deleteRenter",{
              params:{
                userId:userId
            }
            }).then(
                response=>{
                  if (response.data) {
                    this.$message({
                      type: 'success',
                      message: '!'
                    });
                    this.tableData.splice(i, 1)
                  }else {
                    this.$message({
                      type: 'error',
                      message: ''
                    });
                  }
                }
            )

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  },
  created() {
    this.$axios.get(this.$store.state.publicLocation + "/manage/listRenterItemVol").then(
        response => {
          console.log(response.data)
          response.data.forEach(data=>{
            let temp = {
              userId: data.uUserVol.userId,
              roleId: data.uUserVol.roleId,
              roleDescription:data.uRole.description,
              username: data.uUserVol.username,
              password: "",
              sex: data.uUserVol.sex,
              name: data.uUserVol.name,
              phoneNumber: data.uUserVol.phoneNumber,
              identity: data.uUserVol.identity,
              locked: data.uUserVol.locked === "1",
              enable: data.uUserVol.enable === "1",
              createdTime: "2021-11-20 13:19:00",
              updateTime: data.uUserVol.updateTime,
              rentState: data.rentState
            }
            this.tableData.push(temp)
          })


        }
    )
  }
}
</script>

<style scoped>

</style>