<template>
  <el-container>
    <!--        -->
    <el-header>
      <div  class="left" style="float:left;">
        <h1>
          <span class="el-icon-s-home"></span>
          <span v-if="isMobile"></span>
          <span v-else></span>
        </h1>
      </div>
      <div v-show="!isMobile" class="right" style="float:right;">
        <div class="user">
          <div class="userLogo">
            <img src="../../../assets/static/tx.jpg" alt="">
          </div>
        </div>
        <el-dropdown>
          <span>
            <span class="username">{{username}}</span>
            <i class="el-icon-caret-left" style="margin-right: 15px">
            </i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item >
              <a @click.prevent="logout" href="#"></a>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <!--        -->
    <el-main>
      <router-view></router-view>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "MMain",
  data(){
    return {
      username:this.$cookies.get("username")
    }
  },
  computed: {
    isMobile() {
      return this.$store.state.isMobile
    },
    publicLocation(){
      return this.$store.state.publicLocation;
    }
  },
  methods:{
    logout(){
      this.$confirm(`${this.username}?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        });
        this.$cookies.remove("username");
        this.$cookies.remove("JSESSIONID");
        this.$axios.get(this.publicLocation+"/logout")
        this.$router.push({path:"/login"})
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  }
}
</script>

<style scoped>
/**/
.el-header {
  position: sticky;
  top: 0;
  background-color: white;
  font-size: 12px;
  color: #333;
  line-height: 60px;
  z-index: 1;
}

/**/
.left h1 {
  font-size: 22px;
}

/**/
/*user*/
.user {
  display: inline-block;
}

.user .username {
  margin: 0 10px;
  font-size: 15px;
}

/*logo*/
.userLogo {
  display: inline-block;
  height: 40px;
  width: 40px;
}

.userLogo > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  vertical-align: middle;
}

/**/
.el-dropdown .username {
  margin: 0 10px;
  font-size: 15px;
}

.el-dropdown .el-icon-caret-left {
  font-size: 20px;
  transition: all 0.5s;
}

.el-dropdown .username:hover + .el-icon-caret-left,
.el-dropdown .el-icon-caret-left:hover {
  transform: rotate(-90deg);
}

/**/
/**/
.el-main {
  background-color: #f8f8f8;
  padding: 20px;
}

.el-aside {
  color: #333;
}
</style>