<template>
  <div>
    <div>
      <div style="text-align:center;"><h3></h3></div>
      <el-form ref="form" :model="tableData" label-width="100px">
        <el-form-item label="">
          <el-select v-model="tableData.buildingId" clearable placeholder="">
            <el-option
                v-for="item in tableData.options"
                :key="item.buildingId"
                :label="item.name"
                :value="item.buildingId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="tableData.houseNumber" style="width: 21%;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addHouse"></el-button>
          <el-button></el-button>
        </el-form-item>
      </el-form>

    </div>
  </div>
</template>

<script>
export default {
  name: "InsertHouse",
  data() {
    return {
      tableData: {
        buildingId:'',
        houseNumber:"",
        options: [],
      },
      rules:{
        username:[
          {required: true, message: '', trigger: 'blur'},
          {min:4,max:20,message: '4',trigger: 'blur'}
        ],
        password:[
          {required: true, message: '', trigger: 'blur'},
          {min:6,message: '6',trigger: 'blur'}
        ]
      }

    }
  },
  methods:{
    addHouse(){
      if (this.tableData.buildingId===0||
          this.tableData.houseNumber===""||
          !/^[0-9]+$/.test(this.tableData.houseNumber)
      ){
        this.$message.error("")
        return ;
      }
      //
      this.$axios.get(this.$store.state.publicLocation+"/manage/addRoom",{
        params:{
          buildingId:this.tableData.buildingId,
          houseNumber:this.tableData.houseNumber
        }
      }).then(
          response=>{
            if (response.data) {
              this.tableData.houseNumber="";
              this.$message.success("")
            }else {
              this.$message.error("")
            }
          }
      )
      //

      // ajax

    }
  },
  created() {

    this.$axios.get(this.$store.state.publicLocation+"/manage/listBuilding").then(
        response=>{
          response.data.forEach(item=>{
            let temp={
              buildingId:item.buildingId,
              name:item.name
            }
            this.tableData.options.push(temp)

          })

        }
    )

  }
}
</script>

