          <span style="font-size: 16px;margin-left: 35px">{{tableData.buildingInfo.name}}</span>
        </el-form-item>
        <el-form-item label="" disabled>
          <span style="font-size: 16px;margin-left: 35px">{{tableData.houseInfo.houseNumber}}</span>

        </el-form-item>
        <el-form-item label="">
          <el-tooltip class="item" effect="dark" content="" placement="right">
            <el-switch
                disabled
                v-model="tableData.houseState.rentState"
                active-text=""
                inactive-text="">
            </el-switch>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="tableData.houseState.renterName" style="width: 24%;"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
              v-model="tableData.houseState.createdTime"
              type="datetime"
              placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
              v-model="tableData.houseState.stopTime"
              type="datetime"
              placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button></el-button>
        </el-form-item>
      </el-form>
    </div>
      <div v-else style="text-align: center;color: red;font-size: 30px;padding: 50px 0">
