<script>
export default {
  name: "UpdateHouseInfo",
  data() {
    return {
      fall:false,
      tableData:{},
      choose: "",
      options: [
        {
          buildingID: 101,
          name: "A",
        }, {
          buildingID: 102,
          name: "B",
        }, {
          buildingID: 103,
          name: "C",
        },
      ]
    }
  },
  methods:{
    onSubmit(){
      this.$message.success("")
    }
  },
  created() {
    if (this.$route.query.tableData!==undefined) {
      this.tableData = JSON.parse(this.$route.query.tableData)
      this.fall=true;
    }
  }
}
</script>

<style scoped>
.el-form {
  margin-top: 20px;
  padding: 0 50px;
}

</style>