<template>
  <div>
    <div style="text-align:center;"><h3></h3></div>
    <el-form ref="form" :model="form" :rules="rules" label-width="150px">
      <el-form-item prop="choose" label="" required>
        <el-select  v-model="form.choose" clearable placeholder="">
          <el-option
              v-for="item in form.roles"
              :key="item.value"
              :label="item.label"

              :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="username" label="()" required>
        <el-input v-model="form.username"></el-input>
      </el-form-item>
      <el-form-item prop="password" required label="">
        <el-input  v-model="form.password" show-password></el-input>
      </el-form-item>
      <el-form-item prop="name" label="" required>
      <el-input v-model="form.name"></el-input>
    </el-form-item>
      <el-form-item label="">
        <template>
          <el-radio v-model="form.sex" label="0"></el-radio>
          <el-radio v-model="form.sex" label="1"></el-radio>
          <el-radio v-model="form.sex" label="2"></el-radio>
        </template>
      </el-form-item>

      <el-form-item prop="phoneNumber" label="" required>
        <el-input v-model="form.phoneNumber"></el-input>
      </el-form-item>
      <el-form-item prop="identity" label="">
        <el-input  v-model="form.identity"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-switch
            v-model="form.locked"
            active-color="#ff4949"
            inactive-color="#13ce66"
            active-text=""
            inactive-text="">
        </el-switch>
      </el-form-item>
      <el-form-item label="">
        <el-switch
            v-model="form.enable"
            active-color="#13ce66"
            inactive-color="#ff4949"
            active-text=""
            inactive-text="">
        </el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="addRent('form')"></el-button>
        <el-button></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "InsertRenter",
  data() {
    let validateUsername = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (!(4 <= value.length && value.length <= 20)) {
        callback(new Error('4,20'));
      } else if (!/^[\u4E00-\u9FA5A-Za-z0-9]+$/.test(value)) {
        callback(new Error(''))
      } else {
        callback();
      }
    };
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (!/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z\\W]{6,18}$/.test(value)) {
        callback(new Error('6~18'))
      } else {
        callback();
      }
    };
    let checkPhone = (rule, value, callback) => {
      if (value === '') callback(new Error(''))
      else if (!/^1\d{10}$/.test(value)) callback(new Error(''))
      else callback()
    };
    let checkIdentity = (rule, value, callback) => {
      if (value === '') callback()
      else if (!/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(value)) callback(new Error(''))
      else callback()
    };
    return {
      form: {
        choose:'',
        roles:[],
        username:'',
        password:'',
        sex:'2',
        name:'',
        phoneNumber:'',
        identity:'',
        locked:false,
        enable:true,
      },
      rules: {
        choose:[
          {required: true,message:"", trigger: 'blur'}
        ],
        username: [
          // {required: true, message: '', trigger: 'blur'},, message: '4,20'
          {required: true, validator: validateUsername, trigger: 'blur'},
        ],
        password: [
          {required: true, validator: validatePass, trigger: 'blur'}
        ],
        phoneNumber: [
          {required: true, validator: checkPhone, trigger: 'blur'}
        ],
        name:[
          {required:true,message:"", trigger: 'blur'}
        ],
        identity:[
          {validator: checkIdentity, trigger: 'blur'}
        ]
      },
    }
  },
  methods:{
    addRent(formName){
      this.$refs[formName].validate((valid) => {
        if (!valid) this.$message.error("")
        else{
          let params = new URLSearchParams();

          params.append("roleId", this.form.choose)
          params.append("name", this.form.name)
          params.append("sex", this.form.sex)
          params.append("identity", this.form.identity)
          params.append("username", this.form.username)
          params.append("password", this.form.password)
          params.append("phoneNumber", this.form.phoneNumber)
          params.append("locked", this.form.locked?'1':'0')
          params.append("enable", this.form.enable?'1':'0')
          this.$axios.post(this.$store.state.publicLocation + "/manage/addRenter", params).then(
              response=>{
                console.log(response.data)
                if (response.data)this.$message.success("")
                else this.$message.error("")

              }
          )

        }
      })

      // this.$message.success("")
    }
  },
  created() {
    this.$axios(this.$store.state.publicLocation+"/manage/listRole").then(
        response=>{
          response.data.forEach(item=>{
            let temp={
              value:item.roleId,
              label:item.description
            }
            this.form.roles.push(temp)
          })
        }
    )
  }
}
</script>

<style scoped>
.el-form {
  margin-top: 20px;
  padding: 0 50px;
}
.el-input{
  width: 24%;
}
</style>